@color-white:#fff !important;
@blue-color:#499fff;
@grey-color: #a9a9a9;
.boxShadow (@x, @y, @blur, @spread: 0, @alpha) {
    -webkit-box-shadow: @x @y @blur @spread rgba(0, 0, 0, @alpha);
    -moz-box-shadow:    @x @y @blur @spread rgba(0, 0, 0, @alpha);
    -ms-box-shadow: @x @y @blur @spread rgba(0, 0, 0, @alpha);
    -o-box-shadow:    @x @y @blur @spread rgba(0, 0, 0, @alpha);
    box-shadow:     @x @y @blur @spread rgba(0, 0, 0, @alpha);
}
.blue-background {
  background: rgba(63,163,228,1);
  background: -moz-linear-gradient(left, rgba(63,163,228,1) 0%, rgba(63,124,228,1) 50%);
  background: -webkit-gradient(left top, right top, color-stop(0%, rgba(63,163,228,1)), color-stop(50%, rgba(63,124,228,1)));
  background: -webkit-linear-gradient(left, rgba(63,163,228,1) 0%, rgba(63,124,228,1) 50%);
  background: -o-linear-gradient(left, rgba(63,163,228,1) 0%, rgba(63,124,228,1) 50%);
  background: -ms-linear-gradient(left, rgba(63,163,228,1) 0%, rgba(63,124,228,1) 50%);
  background: linear-gradient(to right, rgba(63,163,228,1) 0%, rgba(63,124,228,1) 50%);	
}
body {
	font-family: 'Montserrat', sans-serif;
    background-color: #ffffff;
    font-weight: 300;
    font-size: 14px;
    line-height: 24px;
}
a{
    color: @color-white;
    &:hover,&:focus,&:focus,&:active,&:visited{
        text-decoration: none;  
        color: @color-white;
        opacity: 0.7;
    }    
}

blockquote {
  border:none;
  margin-bottom:-30px;
  position: relative; 
  max-width: 900px; 
  margin: 0 auto; 
  padding-left: 0.4em; 
    p{
  		&:before { 
		    content: "\201C";
		    font-weight: bold;
		    font-size:150px;
		    color: rgba(238, 238, 238, 0.25);
		    position: absolute;
		    top: 0px;
		    left: -100px; 
		    font-family: 'Georgia'; 
        }
        &:after { 
		    content: "\201D";
		    font-weight: bold;
		    font-size:150px;
		    color: rgba(238, 238, 238, 0.25);
		    position: absolute; 
		    bottom: -100px;
		    right: -100px; 
		    font-family: 'Georgia'; 
		}
    }
}
